import React from 'react';
import '../styles/Common.css';
import '../styles/SplashScreen.css';

/*
    This is the SplashScreen component that will be displayed when the game starts.
    It will display a welcome message and a button to start the game.
    Optionally, we can add some instructions or a brief description of the game here
    along with other options such as settings for the game including type of and number
    of cards, and difficulty level.

*/

interface SplashScreenProps {
  onStartGame: () => void; // Prop function to start the game
  setGrid: (x: number, y: number) => void; // Prop function to set the grid
}

// add ui to let the user select the amount of cards

const SplashScreen: React.FC<SplashScreenProps> = ({ onStartGame, setGrid }) => {
  const [numCards] = React.useState<number>(0);

  const isNumCardsValid = numCards >= 1 && numCards <= 10;
  
  const [numCardsX, setNumCardsX] = React.useState<number>(0);
  const [numCardsY, setNumCardsY] = React.useState<number>(0);
  
  const handleNumCardsXChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseInt(event.target.value);
    setNumCardsX(value);
    setGrid(value, numCardsY);
  };
  
  const handleNumCardsYChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseInt(event.target.value);
    setNumCardsY(value);
    setGrid(numCardsX, value);
  };


  return (
    <div className="screen" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
      <h1 style={{ textAlign: 'center' }}>Welcome to the memory game!</h1>
      <p style={{ textAlign: 'center' }}>Select the amount of cards you want to play with</p>
      <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
        <label htmlFor="numCardsX" style={{ marginRight: '10px' }}>Number of Cards (X):</label>
        <input
          type="number"
          id="numCardsX"
          min="1"
          max="10"
          placeholder="Enter the number of cards in X axis"
          style={{
            padding: '10px',
            borderRadius: '5px',
            border: '1px solid #ccc',
            outline: 'none',
            fontSize: '16px',
          }}
          value={numCardsX}
          onChange={handleNumCardsXChange}
        />
      </div>
      <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
        <label htmlFor="numCardsY" style={{ marginRight: '10px' }}>Number of Cards (Y):</label>
        <input
          type="number"
          id="numCardsY"
          min="1"
          max="10"
          placeholder="Enter the number of cards in Y axis"
          style={{
            padding: '10px',
            borderRadius: '5px',
            border: '1px solid #ccc',
            outline: 'none',
            fontSize: '16px',
          }}
          value={numCardsY}
          onChange={handleNumCardsYChange}
        />
      </div>
      <button onClick={onStartGame} disabled={!isNumCardsValid} style={{ opacity: isNumCardsValid ? 1 : 0.5 }}>
        Start Game
      </button>
    </div>
  );
};

export default SplashScreen;